div.elem-group {
    margin: 20px 5px;
}

div.elem-group.inlined {
    width: 30%;
    display: inline-block;
    margin-left: 1%;
}

label {
    display: block;
    font-family: 'Nanum Gothic';
    padding-bottom: 10px;
    font-size: 1.25em;
}

input,
select,
textarea {
    border-radius: 2px;
    border: 2px solid #777;
    box-sizing: border-box;
    font-size: 1.25em;
    font-family: 'Nanum Gothic';
    width: 100%;
    padding: 10px;
}

textarea {
    height: 250px;
}

hr {
    border: 1px dotted #ccc;
}

button.submit-form {
    background: orange;
}