#main-content {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

@media (min-width: 576px) {
    #main-content {
        max-width: 540px;
    }
}

@media (min-width: 992px) {
    #main-content {
        max-width: 960px;
    }
}

@media (min-width: 1200px) {
    #main-content {
        max-width: 1140px;
    }
}

